@import '_imports.scss';
@import '_about.scss';
@import '_header.scss';
@import '_footer.scss';
@import '_sidebar.scss';
@import '_feature.scss';
@import '_modal.scss';



/* Main styles
-------------------------*/

body {
	background-image: $background-image;
	background-position: 100%;
	background-repeat: repeat;
	color: black;
	font-family: 'Droid Sans', sans-serif;
}

.flex-container {
	display: flex;
}

h1 {
	font-family: $frijole;
}

.main {
	background-image: url("../img/background2.jpg");
	background-position: center;
	background-size: cover;

}
#intro {
	padding: 20px;
}

.home {
	display: flex;
	.intro {
		width: 50%;
	}
	.feature {
		width: 50%;
	}
}





/*Error Styles
-----------------------*/
.errorPage{
	height: 500px;
	h1{
		text-align: center;
	}
}


.icn-logo {
  background: #54606b;
  color: #fff;
}
.icn-logo i {
  font-size: 2.45em;
  line-height: 0;
  margin-top: 30px;
}
.icn-error {
  font-size: 5em;
  margin-bottom: .25em;
  margin-right: auto;
  margin-left: auto;
  padding: 0 2em; 
}



/* Gallery CSS
----------------------------*/

.list {
	display: flex;
	list-style: none;
	flex-wrap: wrap;
	li {
		align-self: center;
	}
}

.name {
	color: black;
}

#gallery { 
	ul {
		li {
			display: inline-block;
			margin: 20px; 
			vertical-align: top;
			a {
				text-decoration: none;
			}
			p {
				margin-top: 20px;
				text-align: center; 
			}
			img {
				width: 150px;
				display: inline-block;
			}
			h4 {
				max-width: 150px;
				display: flex;
				justify-content: center;
				align-items: center; 	
			}
		}
	}
}    

h2 {
	text-align: center;
}

.search {
	display: block;
	margin-left: auto;
	margin-right: auto;
}


/* Menu CSS
---------------------*/


#menu {
	button {
		background-color:rgba(0,0,0,0);
		border: none;
		margin: 0;
	}
	li {
		vertical-align: top;
		font-size: 1em;
	}
}



#all {
	font-size: 1em;
	padding: 0;
}
.sub {
	font-size: 0.75em;
}

.supersub {
	padding-left: 0;
}
.supersub li {
	font-size: 0.6em;
	display: block!important;
}



  
.picture {
	position: absolute;
	top: 0;
	width: 100%;
	height: 100%;
	display: flex;
	flex-wrap: wrap;
	background-image: url("../img/overlay.jpg");
}
.picture img {
	padding: 20px;
}

.paragraph {
	margin-left: 20px;
}  


.overlay {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
}

/* Query CSS
---------------------*/
@media (min-width: 768px) {
	.icn-error {
	  padding: 0 4em; 
	}
	.jumbotron h1 {
		width: 80%;
		font-size: 64px;
	}
	#menu {
		li {
			display: block;
		}
		a {
			text-decoration: none;
			color: black;
			font-size: 1.5em;
		}	
}

#all {
	font-size: 1.5em;
}
 	
}


@media (max-width: 768px) and (min-width: 512px) {
	.jumbotron h1 {
		font-size: 1.75em;
	}
	.icn-error {
	  padding: 0 3em; 
	}	
}

@media (min-width: 512px) {
#menu {
	flex-direction: column;
}
.sidebar {
	width: 25%;
}
.main {
	width: 50%;
}
}


