@import '_imports.scss';

.modal-dialog {
	width: 100%;
	margin: 0;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0; 
}

.dialogStyle {
    position: fixed;
    background-image: url("../img/overlay.jpg");
    box-shadow: 0 5px 15px rgba(0,0,0,.5);
    padding: 20px;
    width: 100%;
    height: 100%;

}

.dialog {
	display: flex;
}

.dialogHeader {
	display: flex;
	h3 {
		width: 98%;
		font-family: $frijole;
	}
	button {
	width: 2.5em;
	height: 2.5em;
	text-align: left;
	background-color: #5a5a5a;
	position: fixed;
	z-index: 9999;
	top: 2em;
	right: 2em;
	-webkit-transition: color .3s ease;
	transition: color .3s ease; 
	&:hover	{
		background-color: gray;
	}
	&:focus {
		background-color: gray; 
	}
	&:before{
	  width: 2px;
	  color: #fff;
	  content: 'x';
	  font-size: 2.25em;
	  position: absolute;
	  top: -25%;
	  left: 30%;
	  margin-left: -1px; 	
	}
	&:after {
	  width: 2px;
	  color: #fff;
	  content: 'x';
	  font-size: 2.25em;
	  position: absolute;
	  top: -25%;
	  left: 30%;
	  margin-left: -1px; 
	}
	}
}

.dialogDescription {
	padding: 20px;
	width: 50%;

	p {
		font-weight: bold;
		text-shadow: -1px 0 white, 0 1px white, 1px 0 white, 0 -1px white;
	}
}
